<template><section><p>Here we have three categories for the site types:</p>
<h4>Standard</h4>
<p>A standard website is what most people would need, this can range from a portfolio website to a small business one pager or landing page. These kinds of websites can easily range from €1500 to €2500 excl. VAT.</p>
<p>The price can vary depending on the various features you select, but as a starting point it is what most clients will need.</p>
<h4>E-Commerce</h4>
<p>E-Commerce websites are a whole other story. A lot more thought needs to be added to creating an E-Commerce website, some features like Search Engine Optimisation (SEO), Social Media Optimisation (SMO) and Security are almost a definite must have, and can easily range from €3000 to €6000 excl. VAT.</p>
<h4>Custom Production</h4>
<p>Custom Production websites are the priciest and most advanced kind of website type. This allows the client to request a more specific website equipped with custom functionality that has to be created specifically for them.</p>
<p>This is also the least picked option as the price for these kinds of websites tends to be €8000 or even more than €10 000 excl. VAT, as it requires a lot more hours to be put into the development.</p>
</section></template><script>export default {components:{}}</script>


